<template>
  <div class="home">
    <div class="box">
      <h5>Let's Debug</h5>
      <h1 @click="manage" @contextmenu.prevent="manage">除錯學習平台</h1>
      <input
        type="text"
        autofocus
        placeholder="請輸入學號"
        v-model="userID"
        v-if="!manageMode"
        @keyup.enter="loadData"
      />
      <input
        type="password"
        v-else
        placeholder="請輸入密碼"
        v-model="password"
        @keyup.enter="loadData"
      />
      <p v-text="errorMsg"></p>
      <!-- <p>系統維護中…</p> -->
      <button @click="loadData">進入</button>
    </div>
  </div>
</template>

<script>
import { initializeApp } from "firebase/app";
import { getFirestore, doc, getDoc, setDoc } from "firebase/firestore";
import { ref } from "@vue/reactivity";

export default {
  name: "Login",
  props: { errorMsg: String, today: String, firebaseConfig: Object },
  setup(props, context) {
    const userID = ref("");
    const password = ref("");
    const manageMode = ref(false);
    const loadData = () => {
      if (manageMode.value && password.value === process.env.VUE_APP_MANAGEKEY)
        context.emit("update", { mode: "manage" });
      if (manageMode.value || !userID.value || !/^\d{8}$/.test(userID.value))
        return;
      context.emit("update", { errorMsg: "", loadingMsg: "系統時間同步中…" });
      let hour = 0;
      let logs = {};
      const db = getFirestore(initializeApp(props.firebaseConfig));
      fetch("https://worldtimeapi.org/api/timezone/Asia/Taipei")
        .then((res) => res.json())
        .then((res) => {
          const dt = new Date(res.datetime);
          const format = (num) => num.toString().padStart(2, "0");
          context.emit("update", {
            today: format(dt.getMonth() + 1) + format(dt.getDate()),
          });
          hour = dt.getHours();
          context.emit("update", { errorMsg: "", loadingMsg: "裝置驗證中…" });
          return getDoc(doc(db, "Logs", res.client_ip));
        })
        .then((res) => {
          if (!res.data() || res.data().date !== props.today)
            logs = { date: props.today, userID: [] };
          else logs = { ...res.data() };
          if (logs.userID.length > 10) return Promise.reject([2], "");
          if (logs.userID.indexOf(userID.value) < 0)
            logs.userID.push(userID.value);
          context.emit("update", { errorMsg: "", loadingMsg: "使用者登入中…" });
          setDoc(doc(db, "Logs", res.id), logs);
          return getDoc(doc(db, "User", userID.value));
        })
        .then((res) => {
          if (!res.data()) throw new Error();
          // if (!localStorage.getItem("Debug"))
          //   localStorage.setItem("Debug", props.today + "｜" + userID.value);
          // let temp = localStorage.getItem("Debug").split("｜");
          // if (temp[0] !== props.today)
          //   localStorage.setItem("Debug", props.today + "｜" + userID.value);
          // temp = localStorage.getItem("Debug").split("｜");
          // if (temp[1] !== userID.value) return Promise.reject([1, temp[1]]);
          // else {
          let today = res.data().exList[props.today];
          const exLog = res.data().exLog;
          if (!today) today = res.data().exList["_"];
          if (!today) {
            context.emit("update", {
              account: userID.value,
              peer: [],
              mode: "his",
              loadingMsg: "",
            });
          } else {
            context.emit("update", {
              account: userID.value,
              album: res.data().album,
              loadingMsg: "練習資料下載中…",
            });
            const ex = today.ex.filter(
              (x) =>
                hour >= res.data().time[0] &&
                hour <= res.data().time[1] &&
                (!exLog[props.today] || !exLog[props.today][x])
            );
            if (ex.length > 0)
              context.emit("update", {
                peer: today.peer,
                ex: ex,
                mode: "ex",
              });
            else
              context.emit("update", {
                peer: today.peer,
                mode: "his",
                loadingMsg: "",
              });
          }
          // }
        })
        .catch((arr) => {
          switch (arr[0]) {
            case 1:
              context.emit("update", {
                account: arr[1],
                peer: [],
                mode: "his",
                loadingMsg: "",
              });
              break;
            case 2:
              context.emit("update", {
                mode: "stinky",
                loadingMsg: "",
                errorMsg: "查無此使用者",
              });
              break;
            default:
              setDoc(doc(db, "Error", userID.value), {
                errMsg: arr.toString(),
              });
              userID.value = "";
              // localStorage.removeItem("Debug");
              context.emit("update", {
                mode: "login",
                loadingMsg: "",
                errorMsg: "查無此使用者",
              });
              break;
          }
        });
    };
    let count = -1;
    const manage = (e) => {
      const arr = new Array(6)
        .fill(process.env.VUE_APP_KEYA)
        .fill(process.env.VUE_APP_KEYB, 3, 6);
      count++;
      if (arr[count] !== e.type) count = -1;
      if (arr[count] !== e.type || count < 5) return;
      manageMode.value = true;
    };

    return { userID, password, manage, loadData, manageMode };
  },
};
</script>

<style lang="scss" scoped>
.home {
  min-height: calc(100vh - 10rem);
  display: flex;
  justify-content: center;
  align-items: center;
}
.box {
  width: 350px;
  height: 300px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0px 0px 10px 1px #dddddd;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
h1 {
  margin-top: 0.5rem;
}
h5 {
  margin: 0;
}
input {
  font-size: 20px;
  text-align: center;
  background-color: #eee;
  border: none;
  padding: 10px;
  border-radius: 5px;
  outline: none;
}
p {
  color: #dd3333;
  font-weight: bold;
}
button {
  font-size: 20px;
  padding: 10px 50px;
  font-weight: bold;
  color: #fff;
  border: none;
  border-radius: 5px;
  background-color: var(--main-color);
  transition: box-shadow 0.5s;
  cursor: pointer;
  &:hover {
    box-shadow: 0 0 10px 1px var(--sub-color);
  }
}
</style>
