<template>
  <Loading v-if="loadingMsg.length > 0" :loadingMsg="loadingMsg" />
  <Login
    v-if="mode === 'login' || mode === 'stinky'"
    @update="update"
    :today="today"
    :errorMsg="errorMsg"
    :firebaseConfig="firebaseConfig"
  />
  <Exercise
    v-if="mode === 'ex'"
    @update="update"
    :account="account"
    :today="today"
    :album="album"
    :ex="ex"
    :firebaseConfig="firebaseConfig"
  />
  <History
    v-if="mode === 'his'"
    @update="update"
    :account="account"
    :today="today"
    :peer="peer"
    :firebaseConfig="firebaseConfig"
  />
  <Manage
    v-if="mode === 'manage'"
    :today="today"
    :firebaseConfig="firebaseConfig"
  />
  <div class="stinky" v-if="mode === 'stinky'">
    <p>
      <span>你登入太多不同帳號了，</span><br />
      <span>系統進入「類鎖定」狀態。</span>
    </p>
    <img src="./stinky.png" alt="" />
    <button class="stinky_btn" @click="rick">解除鎖定</button>
  </div>
</template>

<script>
import { reactive, ref } from "@vue/reactivity";
import Login from "./components/Login.vue";
import Loading from "./components/Loading.vue";
import Exercise from "./components/Exercise.vue";
import History from "./components/History.vue";
import Manage from "./components/Manage.vue";
// import { doc, getFirestore, setDoc } from "@firebase/firestore";
import { initializeApp } from "@firebase/app";
import { initializeAppCheck, ReCaptchaV3Provider } from "@firebase/app-check";

export default {
  name: "App",
  components: {
    Login,
    Loading,
    Exercise,
    History,
    Manage,
  },
  setup() {
    const bgc = `#${Math.floor(Math.random() * 4826809 + 729)
      .toString(16)
      .padStart(6, "0")}`;
    const root = document.querySelector(":root");
    root.style.setProperty("--main-color", bgc);
    root.style.setProperty("--sub-color", bgc + "77");
    root.style.setProperty("--light-color", bgc + "44");

    const mode = ref("login");
    const loadingMsg = ref("");
    const errorMsg = ref("");
    const account = ref("");
    const album = ref("");
    const peer = ref();
    const ex = ref();
    const today = ref("");
    const update = (obj) => {
      const check = (str) => str !== undefined;
      if (check(obj.mode)) mode.value = obj.mode;
      if (check(obj.loadingMsg)) loadingMsg.value = obj.loadingMsg;
      if (check(obj.errorMsg)) errorMsg.value = obj.errorMsg;
      if (check(obj.account)) account.value = obj.account;
      if (check(obj.album)) album.value = obj.album;
      if (check(obj.peer)) peer.value = reactive(obj.peer);
      if (check(obj.ex)) ex.value = reactive(obj.ex);
      if (check(obj.today)) today.value = obj.today;
    };

    const firebaseConfig = ref({
      apiKey: process.env.VUE_APP_APIKEY,
      authDomain: process.env.VUE_APP_AUTHDOMAIN,
      databaseURL: process.env.VUE_APP_DATABASEURL,
      projectId: process.env.VUE_APP_PROJECTID,
      storageBucket: process.env.VUE_APP_STORAGEBUCKET,
      messagingSenderId: process.env.VUE_APP_MESSAGINGSENDERID,
      appId: process.env.VUE_APP_APPID,
    });
    document.addEventListener("contextmenu", (e) => e.preventDefault());
    const rick = () =>
      window.open("https://www.youtube.com/watch?v=dQw4w9WgXcQ");
    console.log = function () {};
    console.error = function () {};

    const app = initializeApp(firebaseConfig.value);
    // 產生本地端測試用 token
    // self.FIREBASE_APPCHECK_DEBUG_TOKEN = true;
    // reCAPTCHA v3
    initializeAppCheck(app, {
      provider: new ReCaptchaV3Provider(process.env.VUE_APP_RECAPTCHA),
      isTokenAutoRefreshEnabled: true,
    });

    // 測試
    // const userlist =[];
    // userlist.forEach((obj) => {
    //   const id = obj.id;
    //   delete obj.id;
    //   setDoc(doc(getFirestore(app), "User", id), obj, { merge: true });
    // });

    return {
      mode,
      loadingMsg,
      errorMsg,
      account,
      album,
      peer,
      ex,
      today,
      update,
      firebaseConfig,
      rick,
    };
  },
};
</script>

<style lang="scss">
:root {
  --main-color: black;
  --sub-color: black;
  --light-color: black;
  background-color: var(--sub-color);
}
::-webkit-scrollbar {
  width: min(0.5rem, 1vw);
  height: min(0.4rem, 1vw);
}
::-webkit-scrollbar-thumb {
  border-radius: 0.25rem;
  background: var(--main-color);
}
.stinky {
  position: fixed;
  right: 2rem;
  bottom: 0;
  z-index: 10;
  > p {
    margin: 0;
    padding: 0.5rem 1rem;
    background-color: #fff;
    box-shadow: 0px 0px 5px 1px var(--main-color);
    border-radius: 2rem;
    &::before {
      content: "";
      position: absolute;
      top: 1.5rem;
      left: 3rem;
      background-color: #fff;
      width: 50px;
      height: 50px;
      clip-path: polygon(0% 0%, 50% 0%, 70% 30%);
      transform: translateY(2.5rem);
    }
  }
}
</style>
