<template>
  <div class="wrap" v-if="config !== null">
    <h5 class="main-subTitle">Let's Debug</h5>
    <h1 class="main-title">除錯學習平台</h1>
    <h2 v-text="'題目：' + config.title"></h2>
    <p v-text="config.question"></p>
    <Param
      :input="config.input"
      :outputF="config.outputF"
      :outputT="config.outputT"
    />
    <div class="canvas" id="allCVS">
      <canvas id="codeCVS"></canvas>
      <canvas id="backCVS"></canvas>
      <canvas
        id="frontCVS"
        @mousedown="starDraw"
        @mousemove="drawing"
        @mouseup="endDraw"
        @mouseover="endDraw"
        @click="starWrite"
        @contextmenu.prevent="endWrite"
      ></canvas>
      <div class="textarea" id="tArea">
        <textarea
          class="textBox"
          id="tBox"
          placeholder="說明：
輸入完成請左鍵點擊畫布空白區域
取消輸入請右鍵點擊畫布空白區域
編輯中如切換模式會寫入目前輸入的內容"
          v-model="tBox"
          @keyup.esc="endWrite"
        ></textarea>
      </div>
    </div>
    <p>
      ※請仔細閱讀題目敘述，從程式碼中找出錯誤的地方（不限一處），並使用下方繪圖工具進行註解，註記並修改為您認為正確的程式。
    </p>
    <div class="control options">
      模式：
      <input type="radio" v-model="config.mode" value="pen" id="mPen" />
      <input type="radio" v-model="config.mode" value="line" id="mLine" />
      <input type="radio" v-model="config.mode" value="arrow" id="mArrow" />
      <input type="radio" v-model="config.mode" value="rect" id="mRect" />
      <input type="radio" v-model="config.mode" value="text" id="mText" />
      <input type="radio" v-model="config.mode" value="serial" id="mSerial" />
      <input type="radio" v-model="config.mode" value="clear" id="mClear" />
    </div>
    <div class="control options">
      顏色：
      <input type="radio" v-model="config.color" value="#D60B18" id="cRed" />
      <input type="radio" v-model="config.color" value="#F6BD57" id="cYel" />
      <input type="radio" v-model="config.color" value="#4EBEBA" id="cGrn" />
      <input type="radio" v-model="config.color" value="#1298DB" id="cBlue" />
      <input type="radio" v-model="config.color" value="#000000" id="cBlk" />
    </div>
    <div class="control">
      粗細：
      <input type="range" min="1" max="10" v-model.number="config.lineWidth" />
      <p v-text="config.lineWidth + ' px'"></p>
    </div>
    <div class="control">
      <button
        class="clearBtn"
        @click="
          endWrite();
          resetCVS();
        "
      >
        清除畫布
      </button>
      <button class="resetSBtn" @click="resetSerial()">重置貼紙</button>
      <button
        class="uploadBtn"
        @click="
          writing();
          uploadCVS();
        "
      >
        送出
      </button>
    </div>
  </div>
</template>

<script>
import { initializeApp } from "firebase/app";
import { getFirestore, doc, getDoc, setDoc } from "firebase/firestore";
import { reactive, ref } from "@vue/reactivity";
import { onMounted, watchEffect } from "@vue/runtime-core";
import swal from "sweetalert";
import Param from "./Param.vue";

const initConfig = (ex, cfg) => {
  const config = { ...ex };
  config.code = ex.code.split("\\n");
  config.code.length += 4;
  config.codeLineHeight = 60; // 程式碼渲染行高
  config.width = 1050; // 畫布寬度
  config.height = config.code.length * config.codeLineHeight + 30; // 畫布高度
  config.mode = cfg ? cfg.mode : "pen"; // 預設繪畫模式
  config.color = cfg ? cfg.color : "#D60B18"; // 預設畫筆顏色
  config.lineWidth = cfg ? cfg.lineWidth : 1; // 預設畫筆粗細
  config.fz = cfg ? cfg.fz : 1.1; // 設定字體大小
  config.serial = 1; // 數字貼紙編號
  config.drawFlag = false; // 畫圖中...
  config.writeFlag = false; // 打字中...
  return config;
};
const initRef = (cfg) => {
  cfg.allCVS = document.getElementById("allCVS");
  cfg.codeCVS = document.getElementById("codeCVS");
  cfg.backCVS = document.getElementById("backCVS");
  cfg.frontCVS = document.getElementById("frontCVS");
  cfg.tArea = document.getElementById("tArea");
  cfg.tBox = document.getElementById("tBox");
  cfg.mSerial = document.getElementById("mSerial");
  cfg.codeCTX = cfg.codeCVS.getContext("2d");
  cfg.backCTX = cfg.backCVS.getContext("2d");
  cfg.frontCTX = cfg.frontCVS.getContext("2d");
};
const initCVS = (cfg) => {
  // 設定畫布
  cfg.codeCVS.width = cfg.backCVS.width = cfg.frontCVS.width = cfg.width;
  cfg.codeCVS.height = cfg.backCVS.height = cfg.frontCVS.height = cfg.height;
  // 繪製原始碼畫布
  cfg.codeCTX.font = "2.5rem NotoSans";
  cfg.codeCTX.textBaseline = "top";
  cfg.codeCTX.fillStyle = "#f7f7f7";
  cfg.codeCTX.fillRect(0, 0, 50, cfg.height);
  cfg.codeCTX.fillStyle = "#ffffff";
  cfg.codeCTX.fillRect(50, 0, 948, cfg.height);
  cfg.code.forEach((line, index) => {
    const y = index * cfg.codeLineHeight + 30;
    cfg.codeCTX.textAlign = "end";
    cfg.codeCTX.fillStyle = "#cbb6aa";
    cfg.codeCTX.fillText(index + 1, 40, y);
    cfg.codeCTX.textAlign = "start";
    cfg.codeCTX.fillStyle = "#000000";
    cfg.codeCTX.fillText(line, 55, y - 3);
  });
  // 初始化編號
  initSerial(cfg);
};
// 其他輔助功能
const posX = (cfg, e, x = 0) => {
  const tranX = cfg.backCVS.getBoundingClientRect().left; // 位移距離 X
  return (e.x ? e.x : e.targetTouches[0].pageX) - tranX - cfg.fz * x;
};
const posY = (cfg, e, y = 0) => {
  const tranY = cfg.backCVS.getBoundingClientRect().top; // 位移距離 Y
  return (e.y ? e.y : e.targetTouches[0].pageY) - tranY - cfg.fz * y;
};
const initSerial = (cfg) => {
  cfg.serial = 1;
  cfg.mSerial.style.setProperty("--serial", "'1'");
};
const cursorMode = (cfg) => {
  switch (cfg.mode) {
    case "line":
      cfg.allCVS.style.setProperty("cursor", "grab");
      return;
    case "arrow":
      cfg.allCVS.style.setProperty("cursor", "grab");
      return;
    case "rect":
      cfg.allCVS.style.setProperty("cursor", "crosshair");
      return;
    case "text":
      cfg.allCVS.style.setProperty("cursor", "text");
      return;
    case "serial":
      cfg.allCVS.style.setProperty("cursor", "copy");
      return;
    case "clear":
      cfg.allCVS.style.setProperty("cursor", "crosshair");
      return;
    default:
      cfg.allCVS.style.setProperty("cursor", "default");
  }
};
const drawingCursorMode = (cfg) => {
  switch (cfg.mode) {
    case "line":
      cfg.allCVS.style.setProperty("cursor", "grabbing");
      return;
    case "arrow":
      cfg.allCVS.style.setProperty("cursor", "grabbing");
      return;
    case "clear":
      cfg.allCVS.style.setProperty("cursor", "crosshair");
      return;
  }
};
const setUserText = (ctx, cfg) => {
  ctx.textBaseline = "hanging";
  ctx.font = cfg.fz + "rem Noto Sans TC";
  ctx.fillStyle = cfg.color;
};
const drawLine = (ctx, cfg) => {
  ctx.beginPath();
  ctx.moveTo(cfg.oriX, cfg.oriY);
  ctx.lineTo(cfg.endX, cfg.endY);
  ctx.stroke();
};
const drawArrow = (ctx, cfg) => {
  if (cfg.oriX === cfg.endX || cfg.oriX === cfg.endY) return;
  const dx = cfg.endX - cfg.oriX;
  const dy = cfg.endY - cfg.oriY;
  const headLen = cfg.lineWidth * 3 + 25;
  const angle = Math.atan2(dy, dx);
  ctx.beginPath();
  ctx.moveTo(
    cfg.endX - headLen * Math.cos(angle - Math.PI / 6),
    cfg.endY - headLen * Math.sin(angle - Math.PI / 6)
  );
  ctx.lineTo(cfg.endX, cfg.endY);
  ctx.lineTo(
    cfg.endX - headLen * Math.cos(angle + Math.PI / 6),
    cfg.endY - headLen * Math.sin(angle + Math.PI / 6)
  );
  ctx.stroke();
};
const drawRect = (ctx, cfg) => {
  const w = cfg.endX - cfg.oriX;
  const h = cfg.endY - cfg.oriY;
  ctx.strokeRect(cfg.oriX, cfg.oriY, w, h);
};
const drawSerialIndex = (cfg, e) => {
  (cfg.oriX = posX(cfg, e)), (cfg.oriY = posY(cfg, e));
  (cfg.endX = posX(cfg, e, 4)), (cfg.endY = posY(cfg, e, 9));
};
const drawSerial = (ctx, cfg) => {
  if (cfg.serial > 9) return;
  setUserText(ctx, cfg);
  ctx.fillText(cfg.serial, cfg.endX, cfg.endY);
  ctx.beginPath();
  ctx.lineWidth = 2;
  ctx.arc(cfg.oriX, cfg.oriY, cfg.lineWidth + 15, 0, 2 * Math.PI);
  ctx.stroke();
};
// const half2full = (val) => {
//   return val.replace(/[\u0021-\u007e]/g, (ch) =>
//     String.fromCharCode(ch.charCodeAt(0) + 0xfee0)
//   );
// };

export default {
  name: "Exercise",
  props: {
    account: String,
    today: String,
    album: String,
    ex: Array,
    firebaseConfig: Object,
  },
  components: { Param },
  setup(props, context) {
    const config = ref(null);
    const tBox = ref("");
    let watchMode = null;
    let watchFz = null;
    // let watchH2F = null;
    onMounted(() => loadEx(props.ex[0]));
    const loadEx = (ex) => {
      context.emit("update", { loadingMsg: "練習題目繪製中…" });
      const db = getFirestore(initializeApp(props.firebaseConfig));
      getDoc(doc(db, "Exercise", ex))
        .then(
          (r) => (config.value = reactive(initConfig(r.data(), config.value)))
        )
        .then(() => initRef(config.value))
        .then(() => initCVS(config.value))
        .then(() => {
          context.emit("update", { loadingMsg: "" });
          watchMode = watchEffect(() => {
            if (config.value.mode !== "text") writing();
            endWrite(), cursorMode(config.value);
          });
          watchFz = watchEffect(() => {
            config.value.fz = (config.value.lineWidth + 10) / 10;
            setWriteConfig();
          });
          // watchH2F = watchEffect(() => (tBox.value = half2full(tBox.value)));
        })
        .catch(() =>
          context.emit("update", { ex: [], mode: "his", loadingMsg: "" })
        );
    };
    const reloadEx = (ex) => {
      watchMode(), watchFz(), loadEx(ex); //watchH2F()
    };

    // 計算花費時間
    let time = 0;
    const timing = () => {
      time++, (timer = requestAnimationFrame(timing));
    };
    let timer = requestAnimationFrame(timing);

    // 清除畫布
    const resetCVS = () => {
      swal({
        title: "確定要清除畫布嗎?",
        icon: "warning",
        buttons: ["先不要", "確定"],
        dangerMode: true,
      }).then((flag) => (flag ? initCVS(config.value) : null));
    };
    // 上傳畫布
    const uploadCVS = () => {
      swal({
        title: "確定要送出作答嗎?",
        text: "送出後就不可再更改囉~",
        icon: "info",
        buttons: ["再等等", "確定"],
        dangerMode: true,
      }).then((flag) => (flag ? uploadImage() : null));
    };
    const uploadImage = () => {
      context.emit("update", { loadingMsg: "正在疊加圖層…" });
      const cfg = config.value;
      cfg.frontCTX.clearRect(0, 0, cfg.width, cfg.height);
      cfg.frontCTX.drawImage(cfg.codeCVS, 0, 0);
      cfg.frontCTX.drawImage(cfg.backCVS, 0, 0);
      const title = `${props.ex[0]}_${props.today}`;
      const newForm = new FormData();
      newForm.append("image", cfg.frontCVS.toDataURL().split(",")[1]);
      newForm.append("title", title); // 圖片標題
      newForm.append("description", cfg.question); // 圖片描述
      newForm.append("album", props.album); // 指定相簿id
      fetch("https://api.imgur.com/3/image", {
        method: "post",
        body: newForm,
        headers: { Authorization: process.env.VUE_APP_IMGUR },
      })
        .then((res) => res.json())
        .then((r) => {
          context.emit("update", { loadingMsg: "圖片上傳中…" });
          const db = getFirestore(initializeApp(props.firebaseConfig));
          const obj = {
            exLog: {
              [props.today]: {
                [props.ex[0]]: {
                  ans: cfg.ans,
                  title: cfg.title,
                  input: cfg.input,
                  outputF: cfg.outputF,
                  outputT: cfg.outputT,
                  question: cfg.question,
                  time: time,
                  url: r.data.link,
                },
              },
            },
          };
          return setDoc(doc(db, "User", props.account), obj, { merge: true });
        })
        .then(() => {
          cancelAnimationFrame(timer);
          time = 0;
          timer = requestAnimationFrame(timing);
        })
        .then(() => {
          const ex = [...props.ex];
          ex.shift();
          context.emit("update", { ex: ex });
          if (ex.length > 0) reloadEx(ex[0]);
          else context.emit("update", { ex: ex, mode: "his", loadingMsg: "" });
        });
    };
    // 繪圖模組
    const starDraw = (e) => {
      if (config.value.writeFlag) return;
      const cfg = config.value;
      cfg.drawFlag = true;
      cfg.backCTX.strokeStyle = cfg.frontCTX.strokeStyle = cfg.color;
      cfg.backCTX.lineWidth = cfg.frontCTX.lineWidth = cfg.lineWidth;
      if (cfg.mode === "pen") cfg.backCTX.beginPath();
      const arr = ["line", "arrow", "rect", "clear"];
      if (arr.indexOf(cfg.mode) >= 0) {
        cfg.oriX = cfg.endX = posX(cfg, e);
        cfg.oriY = cfg.endY = posY(cfg, e);
      }
      if (cfg.mode === "serial") drawSerialIndex(cfg, e);
    };
    const drawing = (e) => {
      if (!config.value.drawFlag) return;
      const [cfg, mode] = [config.value, config.value.mode];
      cfg.frontCTX.clearRect(0, 0, 1000, cfg.height);
      drawingCursorMode(cfg);
      (cfg.endX = posX(cfg, e)), (cfg.endY = posY(cfg, e));
      if (mode === "pen")
        cfg.backCTX.lineTo(cfg.endX, cfg.endY), cfg.backCTX.stroke();
      if (mode === "line" || mode === "arrow") drawLine(cfg.frontCTX, cfg);
      if (mode === "arrow") drawArrow(cfg.frontCTX, cfg);
      if (mode === "rect") drawRect(cfg.frontCTX, cfg);
      if (mode === "serial")
        drawSerialIndex(cfg, e), drawSerial(cfg.frontCTX, cfg);
      if (mode === "clear") {
        cfg.frontCTX.fillStyle = "#CCCCCC66";
        cfg.frontCTX.lineWidth = 1;
        const w = cfg.endX - cfg.oriX;
        const h = cfg.endY - cfg.oriY;
        cfg.frontCTX.fillRect(cfg.oriX, cfg.oriY, w, h);
      }
    };
    const endDraw = () => {
      if (!config.value.drawFlag) return;
      const [cfg, mode] = [config.value, config.value.mode];
      cfg.frontCTX.clearRect(0, 0, 1000, cfg.height);
      endWrite(), cursorMode(cfg);
      if (mode === "line" || mode === "arrow") drawLine(cfg.backCTX, cfg);
      if (mode === "arrow") drawArrow(cfg.backCTX, cfg);
      if (mode === "rect") drawRect(cfg.backCTX, cfg);
      if (mode === "serial") {
        drawSerial(cfg.backCTX, cfg);
        cfg.serial++;
        const char = cfg.serial > 9 ? "'9'" : "'" + cfg.serial + "'";
        cfg.mSerial.style.setProperty("--serial", char);
      }
      if (mode === "clear") {
        const w = cfg.endX - cfg.oriX;
        const h = cfg.endY - cfg.oriY;
        cfg.backCTX.clearRect(cfg.oriX, cfg.oriY, w, h);
      }
      cfg.drawFlag = false;
    };
    // 文字模組
    const setWriteConfig = () => {
      const cfg = config.value;
      setUserText(cfg.backCTX, cfg);
      cfg.tBox.style.setProperty("color", cfg.color);
      cfg.tBox.style.setProperty("font-size", cfg.fz + "rem");
      cfg.tBox.style.setProperty("line-height", cfg.fz * 1.2 + "rem");
      cfg.tBox.focus();
    };
    const starWrite = (e) => {
      if (config.value.mode !== "text") return;
      if (config.value.writeFlag === true) {
        writing();
        return;
      }
      const cfg = config.value;
      cfg.writeFlag = true;
      (cfg.oriX = posX(cfg, e)), (cfg.oriY = posY(cfg, e));
      cfg.oriY = cfg.oriY > cfg.height - 24 ? cfg.height - 24 : cfg.oriY;
      cfg.tArea.style.setProperty("top", cfg.oriY + "px");
      cfg.tArea.style.setProperty("left", cfg.oriX + "px");
      cfg.tArea.style.setProperty("z-index", "1");
      setWriteConfig();
    };
    const writing = () => {
      const cfg = config.value;
      const text = cfg.tBox.value.split("\n");
      const arr = [];
      endWrite();
      text.forEach((line) => {
        let len = 0;
        arr.push("");
        line.split("").forEach((char) => {
          len += 1;
          if (len * cfg.fz * 16 > cfg.tArea.offsetWidth)
            arr.push(""), (len = 0);
          arr[arr.length - 1] += char;
        });
      });
      arr.forEach((line, idx) => {
        const y = cfg.oriY + cfg.fz * (3 + idx * 19);
        cfg.backCTX.fillText(line, cfg.oriX, y);
      });
    };
    const endWrite = () => {
      config.value.tArea.style.setProperty("z-index", "-1");
      config.value.tBox.value = "";
      tBox.value = "";
      config.value.writeFlag = false;
    };
    // 重設編號貼紙
    const resetSerial = () => initSerial(config.value);

    return {
      config,
      tBox,
      resetCVS,
      uploadCVS,
      starDraw,
      drawing,
      endDraw,
      starWrite,
      writing,
      endWrite,
      resetSerial,
    };
  },
};
</script>

<style lang="scss" scoped>
.wrap {
  max-width: 1050px;
  margin: 0 auto;
  padding: 1.5rem;
  background-color: #ffffff;
  border-radius: 10px;
}
.main-title {
  margin: 0;
  text-align: center;
}
.main-subTitle {
  margin: 0;
  text-align: center;
}
h2 {
  position: relative;
  z-index: 1;
  &::before {
    content: "";
    position: absolute;
    bottom: 5px;
    display: block;
    width: 250px;
    height: 7px;
    background-color: var(--sub-color);
    z-index: -1;
  }
}
.code {
  display: none;
}
.canvas {
  border: 1px solid #000;
  position: relative;
  overflow: hidden;
  overflow-x: scroll;
  > canvas {
    display: block;
  }
  > canvas + canvas {
    position: absolute;
    top: 0;
  }
}
.textarea {
  position: absolute;
  top: 0;
  left: 0;
  right: 1px;
  bottom: 1px;
  box-sizing: border-box;
  z-index: -1;
  .textBox {
    padding: 0;
    width: 100%;
    height: 100%;
    font-family: "NotoSans", sans-serif;
    background: transparent;
    border: 1px dashed #666;
    resize: none;
    outline: none;
    overflow: hidden;
    &::placeholder {
      font-size: 1rem;
      line-height: 1.2rem;
    }
  }
}

.control {
  width: 100%;
  margin: 1rem 0;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.options > input {
  position: relative;
  width: 2rem;
  height: 2rem;
  margin-left: 1rem;
  display: block;
  cursor: pointer;
  &::after {
    content: "";
    display: flex;
    justify-content: center;
    align-items: center;
    width: calc(2rem - 6px);
    height: calc(2rem - 6px);
    font-size: 1.5rem;
    background-color: #ffffff;
    border: 3px solid transparent;
    border-radius: 5px;
    box-shadow: 1px 1px 5px #d6d6d6;
  }
  &:checked::after {
    border: 3px solid #0075ff;
  }
  &#mPen::after {
    content: "～";
  }
  &#mLine::after {
    content: "／";
  }
  &#mArrow::after {
    content: "↘";
  }
  &#mRect::before {
    content: "";
    position: absolute;
    top: calc(5px + 0.1rem);
    left: 5px;
    width: 1rem;
    height: 0.8rem;
    border: 3px solid #444444;
  }
  &#mText::after {
    content: "T";
  }
  &#mSerial {
    --serial: "1";
  }
  &#mSerial::after {
    content: var(--serial);
  }
  &#mClear::after {
    content: "擦";
    font-size: 1.2rem;
  }
  &#cRed::after {
    background-color: #d60b18;
  }
  &#cYel::after {
    background-color: #f6bd57;
  }
  &#cGrn::after {
    background-color: #4ebeba;
  }
  &#cBlue::after {
    background-color: #1298db;
  }
  &#cBlk::after {
    background-color: #000000;
  }
}
.clearBtn,
.uploadBtn {
  padding: 10px 50px;
  font-size: 20px;
  font-weight: bold;
  color: #ffffff;
  background-color: var(--main-color);
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.clearBtn {
  background-color: #d60b18;
}
.resetSBtn {
  margin-left: 1rem;
  padding: 5px 20px;
  font-size: 14px;
  color: #ffffff;
  background-color: #4ebeba;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.uploadBtn {
  margin-left: auto;
  transition: box-shadow 0.5s;
  &:hover {
    box-shadow: 0 0 10px 1px var(--sub-color);
  }
}
</style>
