<template>
  <div class="loading" id="loading">
    <div class="loading-effect">
      <span class="loading-slice"></span>
      <span class="loading-slice"></span>
      <span class="loading-slice"></span>
      <span class="loading-slice"></span>
      <span class="loading-slice"></span>
      <span class="loading-slice"></span>
    </div>
    <div class="loading-message">
      <p
        v-for="(str, idx) in loadingMsg.split('\n')"
        v-text="str"
        :key="idx"
      ></p>
    </div>
  </div>
</template>

<script>
export default {
  name: "Loading",
  props: { loadingMsg: String },
  setup() {},
};
</script>

<style lang="scss" scoped>
.loading {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #ffffffcc;
  z-index: 99;
  &-effect {
    position: fixed;
    top: 45%;
    left: 50%;
    font-size: 20px;
    display: block;
    width: 3.75em;
    height: 4.25em;
    margin-left: -1.875em;
    margin-top: -2.125em;
    transform-origin: center center;
    transform: rotateY(180deg) rotateZ(-60deg);
    animation: 2s loading-flip steps(2) infinite both;
    @keyframes loading-flip {
      0% {
        transform: rotateY(0deg) rotateZ(-60deg);
      }
      100% {
        transform: rotateY(360deg) rotateZ(-60deg);
      }
    }
  }
  &-slice {
    border-top: 1.125em solid transparent;
    border-right: none;
    border-bottom: 1em solid transparent;
    border-left: 1.875em solid var(--main-color);
    position: absolute;
    top: 0px;
    left: 50%;
    transform-origin: left bottom;
    border-radius: 3px 3px 0 0;
    @for $i from 1 through 6 {
      &:nth-child(#{$i}) {
        transform: rotateZ(60 * $i + deg) rotateY(90deg) rotateX(0);
        animation: 2s preload-cycle-#{$i} linear infinite both;
      }
      @keyframes preload-cycle-#{$i} {
        $startIndex: $i * 5;
        $reverseIndex: (80 - $i * 5);

        #{$startIndex * 1%} {
          transform: rotateZ(60 * $i + deg) rotateY(90deg) rotateX(0deg);
          border-left-color: var(--sub-color);
        }
        #{$startIndex + 5%},
        #{$reverseIndex * 1%} {
          transform: rotateZ(60 * $i + deg) rotateY(0) rotateX(0deg);
          border-left-color: var(--main-color);
        }

        #{$reverseIndex + 5%},
        100% {
          transform: rotateZ(60 * $i + deg) rotateY(90deg) rotateX(0deg);
          border-left-color: var(--sub-color);
        }
      }
    }
  }
  &-message {
    position: fixed;
    top: 55%;
    left: 50%;
    font-size: 30px;
    font-weight: bold;
    text-align: center;
    transform: translateX(-50%);
    > p {
      margin: 0.5rem 0;
    }
  }
  &.hide {
    z-index: -99;
    opacity: 0;
    transition: opacity 1s, z-index 0s ease 3s;
  }
}
</style>
