<template>
  <div class="manage">
    <h5 class="main-subTitle">Let's Debug</h5>
    <h1 class="main-title">除錯學習平台</h1>
    <h5 class="main-subTitle">Manage</h5>
    <select v-model="group">
      <option value="AVQm53w">夜間</option>
      <option value="LEwobX0">日間</option>
      <option value="D7ukhlv">技優</option>
    </select>
    <input type="text" v-model="date" maxlength="4" @keypress.enter="load" />
    <button @click="load">讀取</button>
    <button @click="absent">未參與學生</button>
    <!-- <button @click="allAbsent">從未參與學生</button> -->
    <span v-if="loading">讀取中…</span>
    <div v-if="student.length > 0">
      <p>學號,身分組,參加數,參加周數,低活躍學習者不會與正常學習者分組</p>
      <p v-for="(str, idx) in student" :key="idx" v-text="str"></p>
    </div>
    <div v-else>
      <p>
        <span v-for="(num, idx) in count" :key="idx">
          {{ "完成" + idx + "題：" + num }}｜
        </span>
        <span
          v-text="'約有 ' + Object.keys(feedbackCount).length + ' 人收到回饋'"
        ></span>
        <span>{{
          "｜有 " + Object.keys(abnormal).length + " 人作答異常"
        }}</span>
      </p>
      <p>優>好>普>差</p>
      <div class="data">
        <div class="card" v-for="(item, idx) in data" :key="idx">
          <p>
            學員：{{ item.user }}
            <span :class="item.userLevel">{{
              item.userLevel.split("")[0]
            }}</span>
            ｜同儕：{{ item.peer }}
            <span :class="item.peerLevel">{{
              item.peerLevel.split("")[0]
            }}</span>
          </p>
          <p>
            <span
              v-for="(x, i) in item.feedback"
              :key="i"
              v-text="i === 3 ? x : level[x] + '｜'"
            ></span>
          </p>

          <a :href="item.url" target="_blank">觀看作答結果</a>
        </div>
      </div>
      <hr />
      <p v-if="Object.keys(abnormal).length">
        作答異常，作答時間低於一分鐘的學生
      </p>
      <div class="data">
        <div class="card" v-for="(item, key) in abnormal" :key="key">
          <p>
            學員：{{ key }}
            <span :class="item.level">{{ item.level.split("")[0] }}</span>
          </p>
          <div v-for="(res, idx) in item.result" :key="idx">
            <span> 費時：{{ Math.floor(res.time / 60) }}秒 </span>
            <a :href="res.url" target="_blank">觀看作答結果</a>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div class="manage">
    <h2 class="main-title">登入異常</h2>
    <button @click="loadLog">重整</button>
    <div class="data">
      <div class="card card-sm" v-for="(item, key) in logData" :key="key">
        <p>IP：{{ item.ip }}</p>
        <span>日期：{{ item.date }}&nbsp;</span>
        <button @click="delLog(item.ip)">清除</button>
        <p>{{ item.userID.join(", ") }}</p>
      </div>
    </div>
  </div>
  <div class="manage">
    <h2 class="main-title">登入失敗</h2>
    <button @click="loadErr">重整</button>
    <div class="data">
      <div class="card card-sm" v-for="(item, key) in errData" :key="key">
        <p>ID：{{ item.id }}</p>
        <p>{{ item.errMsg }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import {
  collection,
  deleteDoc,
  doc,
  getDocs,
  getFirestore,
  query,
  where,
} from "@firebase/firestore";
import { initializeApp } from "@firebase/app";
import { reactive, ref } from "@vue/runtime-core";
export default {
  name: "Manage",
  props: { today: String, firebaseConfig: Object },
  setup(props) {
    const level = reactive(["優", "好", "普", "差"]);
    const loading = ref(false);
    const group = ref("AVQm53w");
    const date = ref("");
    const count = reactive(new Array(4).fill(0));
    const feedbackCount = reactive({});
    const data = reactive([]);
    const abnormal = reactive({});
    const student = reactive([]);
    const logData = reactive([]);
    const errData = reactive([]);

    fetch("https://worldtimeapi.org/api/timezone/Asia/Taipei")
      .then((res) => res.json())
      .then((r) => {
        const dt = new Date(r.datetime);
        const format = (num) => num.toString().padStart(2, "0");
        date.value = format(dt.getMonth() + 1) + format(dt.getDate());
      });

    const db = getFirestore(initializeApp(props.firebaseConfig));
    const load = () => {
      loading.value = true;
      student.length = 0;
      for (const key in abnormal) {
        delete abnormal[key];
      }
      for (const key in feedbackCount) {
        delete feedbackCount[key];
      }
      const q = query(
        collection(db, "User"),
        where("album", "==", group.value)
      );
      getDocs(q)
        .then((r) => {
          count.fill(0);
          data.length = 0;
          r.docs.forEach((user) => {
            const exLog = user.data().exLog[date.value];
            if (!exLog) count[0]++;
            else count[Object.entries(exLog).length]++;
            if (exLog) {
              Object.entries(exLog).forEach((log) => {
                if (log[1].time < 3600) {
                  if (!abnormal[user.id]) abnormal[user.id] = { result: [] };
                  abnormal[user.id].level = user.data().level;
                  abnormal[user.id].time = abnormal[user.id].time;
                  abnormal[user.id].result.push({
                    time: log[1].time,
                    url: log[1].url,
                  });
                }
                if (log[1].feedback) {
                  Object.entries(log[1].feedback).forEach((ex) => {
                    feedbackCount[ex[0]] = feedbackCount[ex[0]]
                      ? feedbackCount[ex[0]] + 1
                      : 1;
                    const peerLevel = r.docs.filter((x) => x.id === ex[0])[0]
                      ? r.docs.filter((x) => x.id === ex[0])[0].data().level
                      : "Low";
                    if (ex[1].length === 4) {
                      data.push({
                        user: user.id,
                        userLevel: user.data().level,
                        peer: ex[0],
                        peerLevel: peerLevel,
                        feedback: ex[1],
                        url: log[1].url,
                      });
                    }
                  });
                }
              });
            }
          });
        })
        .then(() => (loading.value = false));
    };
    const absent = () => {
      loading.value = true;
      student.length = 0;
      const q = query(
        collection(db, "User"),
        where("album", "==", group.value)
      );
      getDocs(q)
        .then((r) => {
          r.docs.forEach((user) => {
            if (!user.data().exLog[date.value]) {
              const his = Object.keys(user.data().exLog);
              let str = `${user.id},${user.data().level.split("")[0]},`;
              str += `${his.length},[${his}]`;
              if (!user.data().mark || user.data().mark === "True")
                str += `｜低活躍學習者`;
              student.push(str);
            }
          });
        })
        .then(() => (loading.value = false));
    };
    const allAbsent = () => {
      loading.value = true;
      student.length = 0;
      const q = query(
        collection(db, "User"),
        where("album", "==", group.value)
      );
      getDocs(q)
        .then((r) => {
          r.docs.forEach((user) => {
            if (Object.entries(user.data().exLog).length === 0) {
              const his = Object.keys(user.data().exLog);
              let str = `${user.id},${user.data().level.split("")[0]},`;
              str += `${his.length},[${his}]`;
              if (!user.data().mark || user.data().mark === "True")
                str += `｜低活躍學習者`;
              student.push(str);
            }
          });
        })
        .then(() => (loading.value = false));
    };
    const loadLog = () => {
      logData.length = 0;
      const q = query(collection(db, "Logs"));
      getDocs(q)
        .then((r) => {
          r.docs.forEach((user) => {
            if (user.data().userID.length >= 2) {
              const obj = user.data();
              (obj.ip = user.id), logData.push(obj);
            }
          });
        })
        .then(() => logData.sort((a, b) => b.date - a.date));
    };
    const delLog = (ip) => deleteDoc(doc(db, "Logs", ip)).then(() => loadLog());
    const loadErr = () => {
      errData.length = 0;
      const q = query(collection(db, "Error"));
      getDocs(q)
        .then((r) => {
          r.docs.forEach((user) => {
            const obj = user.data();
            (obj.id = user.id), errData.push(obj);
          });
        })
        .then(() => logData.sort((a, b) => b.date - a.date));
    };

    return {
      level,
      loading,
      group,
      date,
      count,
      feedbackCount,
      data,
      abnormal,
      student,
      logData,
      errData,
      load,
      absent,
      allAbsent,
      loadLog,
      delLog,
      loadErr,
    };
  },
};
</script>

<style lang="scss" scoped>
.manage {
  position: relative;
  max-width: 1000px;
  margin: 0 auto;
  padding: 1.5rem;
  background-color: #ffffff;
  border-radius: 10px;
  & + .manage {
    margin-top: 1rem;
  }
}

.main-title {
  margin: 0;
  text-align: center;
}
.main-subTitle {
  margin: 0;
  text-align: center;
}
.data {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  flex-wrap: wrap;
}
.card {
  margin: 1rem 0;
  padding: 1rem;
  width: 49%;
  background-color: #fafafa;
  box-sizing: border-box;
  > p {
    margin: 0.5rem 0;
  }
  &-sm {
    width: 32%;
    padding: 0.5rem;
  }
}
.High,
.Middle,
.Low {
  padding: 0.1rem 0.5rem;
  color: #fff;
}
.High {
  background-color: #6666ff;
}
.Middle {
  background-color: #00cc22;
}
.Low {
  background-color: #ff6666;
}
</style>
