<template>
  <div>
    <div class="history" v-if="detail.id">
      <h5 class="main-subTitle">Let's Debug</h5>
      <h1 class="main-title">除錯學習平台</h1>
      <h2>給予同學回饋</h2>
      <h3 v-text="'題目：' + detail.title"></h3>
      <p v-text="detail.question"></p>
      <Param
        :input="detail.input"
        :outputF="detail.outputF"
        :outputT="detail.outputT"
      />
      <h3>作答結果</h3>
      <img :src="detail.url" :alt="detail.question" />
      <h3>我給同學的意見回饋</h3>
      <div v-for="(item, idx) in level" :key="idx">
        <h4 v-if="idx === 0">1. 除錯程式完成度（必填）</h4>
        <h4 v-if="idx === 1">2. 註解說明清晰度（必填）</h4>
        <h4 v-if="idx === 2">3. 對我的啟發性（必填）</h4>
        <p>我認為這位同學...</p>
        <div class="radio" v-for="(ans, i) in item" :key="i">
          <input
            :id="'radio-' + idx + i"
            :name="'ans' + idx"
