            :value="i"
            v-model="detail.feedback[idx]"
          />
          <label :for="'radio-' + idx + i" class="radio-label" v-text="ans">
          </label>
        </div>
      </div>
      <h4>4. 心得與回饋</h4>
      <textarea
        cols="30"
        rows="4"
        maxlength="300"
        v-model="detail.feedback[3]"
        placeholder="非必填，可給予同學建議、鼓勵、回饋或分享自己解題思路"
      ></textarea>
      <div class="btn">
        <button class="cancel" @click="clearId">返回</button>
        <button class="sendFeedback" @click="sendFeedback">送出回饋</button>
      </div>
    </div>
    <div class="history" v-if="detail.ex">
      <h5 class="main-subTitle">Let's Debug</h5>
      <h1 class="main-title">除錯學習平台</h1>
      <h2>查看過去練習的資料</h2>
      <h3 v-if="detail.count > 0">同儕給我的回饋</h3>
      <div class="exLog">
        <div class="peerCard" v-for="(item, idx) in detail.feedback" :key="idx">
          <h4>除錯程式完成度</h4>
          <p v-text="level[0][item[0]]"></p>
          <h4>註解說明清晰度</h4>
          <p v-text="level[1][item[1]]"></p>
          <h4>對我的啟發性</h4>
          <p v-text="level[2][item[2]]"></p>
          <h4>心得與回饋</h4>
          <p v-text="item[3]"></p>
        </div>
      </div>
      <h3 v-text="'題目：' + detail.title"></h3>
      <p v-text="detail.question"></p>
      <Param
        :input="detail.input"
        :outputF="detail.outputF"
        :outputT="detail.outputT"
      />
      <h3 v-text="'練習日期：' + detail.date"></h3>
      <h3>作答結果</h3>
      <img :src="detail.url" :alt="detail.question" />
      <div>
        <h3>參考答案</h3>
        <canvas id="codeCVS"></canvas>
      </div>

      <div class="btn">
        <button class="cancel" @click="clearEx">返回</button>
      </div>
    </div>
    <div class="history" v-if="!detail.id && !detail.ex">
      <a
        class="report"
        href="https://forms.gle/pkqnK6UeWt46gjMH6"
        target="_blank"
      >
        意見回報
      </a>
      <div class="hattifatteners">
        <img src="../hattifatteners.png" alt="" />
      </div>
      <div class="easter moomin">
        <p>你知道嗎?嚕嚕咪才不是河馬!(2/3)</p>
        <img src="../moomin.png" alt="" />
      </div>
      <h5 class="main-subTitle" @click="moominma++">Let's Debug</h5>
      <h1 class="main-title">除錯學習平台</h1>
      <h2 v-if="(!peerAllDone[0] || !peerAllDone[1]) && peer.length">
        同學今日的作答結果
        <small>※待其他同學練習結束後點擊出現的結果給予回饋。</small>
      </h2>
      <p v-else>※全部都審閱完並給予回饋了，你真棒!｡:.ﾟヽ(*´∀`)ﾉﾟ.:｡</p>
      <div v-if="(!peerAllDone[0] || !peerAllDone[1]) && peer.length">
        <div v-for="(item, idx) in peerUnReply" :key="idx">
          <div v-if="!peerAllDone[idx]">
            <h3 v-text="`同學 ${String.fromCharCode(65 + idx)}`"></h3>
            <div class="exLog">
              <div
                v-text="
                  `同學 ${String.fromCharCode(65 + idx)} 還在作答中，再等一下`
                "
                v-if="item.length === 0"
              ></div>
              <div class="exLog-item" v-for="ex in item" :key="ex.key">
                <div
                  class="exLog-card"
                  @click="loadFeedback(ex)"
                  v-if="!ex.feedback || !ex.feedback[account]"
                >
                  <h4 class="exLog-title" v-text="ex.title"></h4>
                  <span class="exLog-unread">！</span>
                </div>
              </div>
            </div>
          </div>
          <p
            v-else
            v-text="
              `※已經審閱完 ${String.fromCharCode(
                65 + idx
              )} 同學全部的練習並給予回饋，辛苦了~d(d＇∀＇)`
            "
          ></p>
        </div>
      </div>
      <div class="space"></div>
      <h2>
        我每周的練習紀錄
        <small>
          ※點擊下方題目可以觀看您的<strong>參考答案</strong>與其他同學給你的<strong>回饋</strong>。
        </small>
      </h2>
      <div class="easter maiden">
        <p>現在的小孩還知道嚕嚕咪嗎(1/3)</p>
        <img src="../maiden.png" alt="" />
      </div>
      <div
        v-for="item in exLog"
        :key="item[0].date"
        :class="today === item[0].date ? 'highlight' : ''"
      >
        <h3 class="title">
          <span v-text="item[0].date"></span>&nbsp;
          <strong v-if="today === item[0].date">New!</strong>
        </h3>
        <div class="exLog">
          <div class="exLog-item" v-for="ex in item" :key="ex.key">
            <div class="exLog-card" @click="loadDetail(ex)">
              <h4 class="exLog-title" v-text="ex.title"></h4>
              <span
                class="exLog-unread"
                v-text="ex.unread"
                v-if="ex.unread > 0"
              ></span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="easter moominma" :class="moominma == 7 ? 'show' : ''">
      <p>這個彩蛋藏這麼深，你是怎麼發現的!(3/3)</p>
      <img src="../moominma.png" alt="" />
    </div>
  </div>
</template>

<script>
import { reactive, ref } from "@vue/reactivity";
import { doc, getFirestore, onSnapshot, setDoc } from "@firebase/firestore";
import { initializeApp } from "@firebase/app";
import swal from "sweetalert";
import Param from "./Param.vue";

const initCVS = (ans) => {
  const codeCVS = document.getElementById("codeCVS");
  const codeCTX = codeCVS.getContext("2d");
  // 設定畫布
  codeCVS.width = 1000;
  codeCVS.height = ans.split("\\n").length * 40 + 15;
  // 繪製原始碼畫布
  codeCTX.font = "2rem NotoSans";
  codeCTX.textBaseline = "top";
  codeCTX.fillStyle = "#f7f7f7";
  codeCTX.fillRect(0, 0, 50, codeCVS.height);
  codeCTX.fillStyle = "#ffffff";
  codeCTX.fillRect(50, 0, 948, codeCVS.height);
  ans.split("\\n").forEach((line, index) => {
    const y = index * 40 + 15;
    codeCTX.textAlign = "end";
    codeCTX.fillStyle = "#cbb6aa";
    codeCTX.fillText(index + 1, 40, y);
    codeCTX.textAlign = "start";
    codeCTX.fillStyle = "#000000";
    codeCTX.fillText(line, 55, y - 3);
  });
};

export default {
  name: "History",
  props: {
    account: String,
    today: String,
    peer: Array,
    firebaseConfig: Object,
  },
  components: { Param },
  setup(props, context) {
    const moominma = ref(0);
    const level = reactive([
      [
        "一定是大拇指的啦👍：有找出程式中所有的錯誤",
        "做得好：有找出程式中大多數的錯誤",
        "還不賴：有找出程式中部分錯誤，可惜有一些還是錯的",
        "不優喔：沒有找到任何錯誤欸(๑•́ ₃ •̀๑)",
      ],
      [
        "一定是大拇指的啦👍：註解的內容非常清楚且容易理解",
        "做得好：註解內容還蠻清楚的，基本能理解",
        "還不賴：註解說明的內容稍微艱澀難懂，需要花一點時間理解",
        "不優喔：註解的內容與題目沒什麼關連或是沒有任何註解",
      ],
      [
        "一定是大拇指的啦👍：註解內容對我有所啟發，從中學習到不同的程式語法與新的思考方式",
        "做得好：註解內容對我有些幫助，能使我較清楚理解問題",
        "還不賴：部分內容可能對我多少有點幫助，大概(?)",
        "不優喔：註解說明無助於我，叫我阿嬤來還比較快",
      ],
    ]);
    const db = getFirestore(initializeApp(props.firebaseConfig));

    const exLog = reactive([]);
    const starting = {};
    onSnapshot(doc(db, "User", props.account), (r) => {
      exLog.length = 0;
      const temp = Object.entries(r.data().exLog).sort((a, b) => b[0] - a[0]);
      temp.forEach(([date, val], idx) => {
        exLog.push([]);
        const temp2 = Object.entries(val).sort(
          (a, b) => b[0].replace("Ex", "") - a[0].replace("Ex", "")
        );
        temp2.forEach(([key, val]) => {
          (val.key = key), (val.date = date);
          val.count = val.feedback ? Object.entries(val.feedback).length : 0;
          starting[date + key] =
            date + key in starting ? starting[date + key] : val.count;
          val.unread = val.count - starting[date + key];
          exLog[idx].push(val);
        });
      });
    });
    const peerUnReply = reactive([[], []]);
    const peerAllDone = reactive([false, false]);
    props.peer.forEach((peer, idx) => {
      onSnapshot(doc(db, "User", peer), (r) => {
        if (!r.data().exLog[props.today]) {
          peerAllDone[idx] = false;
          return;
        }
        const newArr = [];
        peerUnReply[idx] = [];
        Object.entries(r.data().exLog[props.today]).forEach(([key, val]) => {
          (val.key = key), (val.id = peer);
          if (!val.feedback || (val.feedback && !val.feedback[props.account]))
            peerUnReply[idx].push(val);
          newArr.push(val);
        });
        peerUnReply[idx] = peerUnReply[idx].sort((a, b) => a.key - b.key);
        peerAllDone[idx] = newArr.length === 3 && peerUnReply[idx].length === 0;
      });
    });

    const detail = reactive({});
    const loadFeedback = (ex) => {
      context.emit("update", { loadingMsg: "資料載入中…" });
      Object.entries(ex).forEach(([key, val]) => (detail[key] = val));
      detail.feedback = [];
      setTimeout(() => context.emit("update", { loadingMsg: "" }), "1000");
    };
    const sendFeedback = () => {
      const ans = detail.feedback;
      const verify =
        ans[0] === undefined || ans[1] === undefined || ans[2] === undefined;
      if (verify) swal("有未填的選項!", "", "error");
      if (verify) return;
      swal({
        title: "確定要送出嗎?",
        icon: "info",
        buttons: ["先不要", "確定"],
        dangerMode: true,
      }).then((flag) => (flag ? uploadFeedback() : null));
    };
    const uploadFeedback = () => {
      context.emit("update", { loadingMsg: "正在添加糖、香料還有美好的味道" });
      const obj = {
        exLog: {
          [props.today]: {
            [detail.key]: {
              feedback: {
                [props.account]: detail.feedback,
              },
            },
          },
        },
      };
      setDoc(doc(db, "User", detail.id), obj, { merge: true })
        .then(() => clearId())
        .then(() => context.emit("update", { loadingMsg: "" }));
    };
    const clearId = () => delete detail.id;
    const loadDetail = (ex) => {
      (ex.unread = 0), (starting[ex.date + ex.key] = ex.count);
      context.emit("update", { loadingMsg: "資料載入中…" });
      (detail.date = ex.date), (detail.ex = ex.key), (detail.url = ex.url);
      (detail.count = ex.count), (detail.question = ex.question);
      (detail.title = ex.title), (detail.ans = ex.ans);
      (detail.outputF = ex.outputF), (detail.outputT = ex.outputT);
      (detail.input = ex.input), (detail.feedback = ex.feedback);
      setTimeout(() => initCVS(detail.ans), "1000");
      setTimeout(() => context.emit("update", { loadingMsg: "" }), "1000");
    };
    const clearEx = () => delete detail.ex;

    window.onbeforeunload = () => false;

    return {
      moominma,
      level,
      exLog,
      peerUnReply,
      peerAllDone,
      detail,
      loadFeedback,
      sendFeedback,
      clearId,
      loadDetail,
      clearEx,
    };
  },
};
</script>

<style lang="scss" scoped>
.easter {
  position: fixed;
  bottom: 0;
  z-index: 10;
  transition: transform 1s 0s;
  &.moomin {
    right: 2rem;
    transform: translateX(150%);
  }
  &.maiden {
    left: 2rem;
    transform: translateX(-150%);
  }
  &.moominma {
    left: 50%;
    transform: translateX(-50%) translateY(150%);
    &.show {
      transform: translateX(-50%) translateY(0%);
      transition: transform 1s;
    }
  }
  & > p {
    margin: 0;
    padding: 0.5rem 1rem;
    background-color: #fff;
    box-shadow: 0px 0px 5px 1px var(--main-color);
    border-radius: 2rem;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 3rem;
      background-color: #fff;
      width: 50px;
      height: 50px;
      clip-path: polygon(0% 0%, 50% 0%, 70% 30%);
      transform: translateY(2.5rem);
    }
  }
}
.hattifatteners {
  position: absolute;
  top: 0;
  right: 4rem;
  width: 50px;
  height: 50px;
  transform: translateY(0%);
  transition: transform 0.5s 2s;
  z-index: -1;
  > img {
    width: 100%;
    height: 100%;
  }
}
.history {
  position: relative;
  max-width: 1000px;
  margin: 0 auto;
  padding: 1.5rem;
  background-color: #ffffff;
  border-radius: 10px;
}
.main-title {
  margin: 0;
  text-align: center;
}
.main-subTitle {
  margin: 0;
  text-align: center;
}
.radio {
  margin: 0 0.5rem;
  input[type="radio"] {
    position: absolute;
    opacity: 0;
    + .radio-label {
      display: inline-block;
      padding: 0.5rem;
      cursor: pointer;
      &:before {
        content: "";
        background: #f4f4f4;
        border-radius: 100%;
        border: 1px solid darken(#f4f4f4, 25%);
        display: inline-block;
        width: 1.4em;
        height: 1.4em;
        position: relative;
        margin-right: 0.5rem;
        vertical-align: top;
        text-align: center;
        transition: all 250ms ease;
      }
    }
    &:checked {
      + .radio-label {
        &:before {
          background-color: var(--main-color);
          box-shadow: inset 0 0 0 4px #f4f4f4;
        }
      }
    }
    &:focus {
      + .radio-label {
        &:before {
          outline: none;
          border-color: var(--main-color);
        }
      }
    }
  }
}
.space {
  position: relative;
  margin: 3rem auto;
  width: 15px;
  height: 15px;
  background-color: var(--main-color);
  transform: rotate(45deg);
  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 0;
    width: 15px;
    height: 15px;
    background-color: var(--main-color);
    transform: translate(-50px, 50px);
  }
  &::after {
    transform: translate(50px, -50px);
  }
}
img {
  -webkit-user-drag: none;
}
textarea {
  width: 100%;
  font-size: 1rem;
  padding: 0.25rem;
  font-family: "Noto Sans TC", sans-serif;
  border-radius: 5px;
  box-sizing: border-box;
  resize: none;
}
.btn {
  display: flex;
  justify-content: center;
  padding-top: 1rem;
}
.sendFeedback {
  font-size: 20px;
  padding: 10px 50px;
  font-weight: bold;
  color: #fff;
  border: none;
  border-radius: 5px;
  background-color: var(--main-color);
  transition: box-shadow 0.5s;
  cursor: pointer;

  &:hover {
    box-shadow: 0 0 10px 1px var(--sub-color);
  }
}
.cancel {
  position: absolute;
  top: 2rem;
  left: 2rem;
  font-size: 1rem;
  padding: 0.5rem 1rem;
  color: #fff;
  border: none;
  border-radius: 5px;
  background-color: #999999;
  transition: box-shadow 0.5s;
  cursor: pointer;
  &:hover {
    box-shadow: 0 0 5px 1px #333333;
  }
}
.report {
  position: absolute;
  top: 2rem;
  right: 2rem;
  font-size: 1rem;
  padding: 0.5rem 1rem;
  color: #fff;
  border: none;
  border-radius: 5px;
  background-color: var(--main-color);
  transition: box-shadow 0.5s;
  text-decoration: none;
  cursor: pointer;
  &:hover {
    box-shadow: 0 0 5px 1px var(--sub-color);
  }
}
.report:hover + .hattifatteners {
  transform: translateY(-80%);
}
.report:hover + .hattifatteners + .moomin {
  transform: translateX(0%);
  transition: transform 1s 5s;
}
.peerCard {
  width: 44%;
  margin: 1rem 3%;
  padding: 1rem;
  box-sizing: border-box;
  background-color: #fafafa;
  border-radius: 5px;
  h4 {
    margin: 0;
  }
  p {
    margin: 0.25rem 0;
    margin-left: 1.25rem;
  }
}
h2 {
  position: relative;
  z-index: 1;
  &::before {
    content: "";
    position: absolute;
    bottom: 5px;
    display: block;
    width: 150px;
    height: 7px;
    background-color: var(--sub-color);
    z-index: -1;
  }
  small {
    margin-left: 1rem;
    font-size: 1rem;
    font-weight: initial;
  }
  &:hover + .maiden {
    transform: translateX(0%);
    transition: transform 1s 3s;
  }
}
.exLog {
  display: flex;
  align-items: stretch;
  margin-bottom: 1rem;
  &-item {
    width: 31%;
    margin: 0 1%;
  }
  &-card {
    position: relative;
    width: 100%;
    padding: 1rem;
    background-color: #fafafa;
    border-radius: 5px;
    transition: box-shadow 0.5s;
    box-sizing: border-box;
    &:hover {
      box-shadow: inset 0 0 5px 1px var(--sub-color);
      cursor: pointer;
    }
  }
  &-title {
    margin: 0;
  }
  &-unread {
    position: absolute;
    top: 0;
    right: 0;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #ffffff;
    border-radius: 50%;
    background-color: #d60b18;
    transform: translate(20%, -20%);
  }
}
strong {
  color: var(--main-color);
}
.highlight {
  padding: 1px 0;
  background-color: var(--light-color);
  border-radius: 10px;
}
h3.title {
  margin-left: 1rem;
}
</style>
